/** @format */

import React from "react";
import ProgressHeader from "../../Components/UI/ProgressHeader/ProgressHeader";
import classes from "../AccountDetail/AccountDetail.module.css";
import css from "./Location.module.css";
import MapContainer from "./Map/MapContainer/MapContainer";
import BackBtn from "../../Components/UI/BackBtn/BackBtn";

const Location = (props) => {
  return (
    <section className={css.Location}>
      <ProgressHeader progress={70} />
      <main>
        <div className={classes.Form_Header}>
          <BackBtn />
          <div className={classes.Header_Text}>
            <h1>Tag a Business Location</h1>
          </div>
        </div>
        <MapContainer />
      </main>
    </section>
  );
};

export default Location;
