/** @format */

import React from "react";
import { GoogleApiWrapper } from "google-maps-react";
import { MAP } from "../../../../Backend";

import CurrentLocation from "../Map";

class MapContainer extends React.Component {
  state = {
    showingInfoWindow: false, //Hides or the shows the infoWindow
    activeMarker: {}, //Shows the active marker upon click
    selectedPlace: {}, //Shows the infoWindow to the selected place upon a marker
    currentLocation: {
      lat: this.props.location && this.props.location.lat,
      lng: this.props.location && this.props.location.lng,
    },
  };

  render() {
    return (
      <CurrentLocation
        handleMapValues={this.props.handleMapValues}
        inputStyle={this.props.inputStyle}
        inputStyleContainer={this.props.inputStyleContainer}
        height={this.props.height}
        authStepSet={this.props.authStepSet}
        centerAroundCurrentLocation={true}
        google={this.props.google}
        onClick={this._onClick}
        currentLocation={this.props.location}
      />
    );
  }
}

export default GoogleApiWrapper({
  apiKey: MAP,
})(MapContainer);
