/** @format */

import React, { Component } from "react";
import ReactDOM from "react-dom";
import { connect } from "react-redux";
import "./Map.css";
import * as actions from "../../../Store/actions/index";
import { withRouter } from "react-router-dom";
import { updateObject } from "../../../Shared/Utility";
import { UIButton } from "../../../Components/exports";

const mapStyles = {
  map: {
    // position: "absolute",
    width: "100%",
    height: "50vh",
    margin: "auto",
  },
};

export class CurrentLocation extends Component {
  constructor(props) {
    super(props);

    const { lat, lng } = this.props.initialCenter;
    this.state = {
      currentLocation: {
        lat: lat,
        lng: lng,
      },
      markers: "",
      userData: {
        searchValue: {
          value: "",
          elementType: "text",
          required: true,
          type: "text",
          placeholder: "Search Place..",
          id: 1,
        },
      },
    };

    this.mapRef = React.createRef();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.google !== this.props.google) {
      console.log("search");
      this.loadMap();
    }
    if (prevState.currentLocation !== this.state.currentLocation) {
      this.recenterMap();
    }
  }

  inputChangedHandler = (event, inputIdentifier) => {
    const { userData } = this.state;
    const updatedFormElement = updateObject(userData[inputIdentifier], {
      value: event.target.value,
    });

    const updatedOrderForm = updateObject(userData, {
      [inputIdentifier]: updatedFormElement,
    });

    this.setState({ ...this.state, userData: updatedOrderForm });
  };

  recenterMap = () => {
    if (this.props.currentLocation) {
      const map = this.map;

      console.log(map);
      const current = this.props.currentLocation;

      const google = this.props.google;
      const maps = google.maps;

      if (map) {
        let center = new maps.LatLng(current.lat, current.lng);
        map.panTo(center);
      }
    }
  };

  loadMap = () => {
    const { google } = this.props;
    const maps = google.maps;
    if (this.props && this.props.google && maps) {
      // Check if google is Available

      //   Reference to the actual DOM Element
      const node = ReactDOM.findDOMNode(this.mapRef.current);

      const { zoom } = this.props;

      const { lat, lng } = this.state.currentLocation;
      const center = new maps.LatLng(lat, lng);
      const mapConfig = Object.assign(
        {},
        { center: center, zoom: zoom, mapTypeControl: false }
      );

      // maps.map() is constructor that instantiate the map
      this.map = new maps.Map(node, mapConfig);

      const searchBox = new maps.places.SearchBox(
        document.querySelector("#input")
      );

      this.map.addListener("bounds_changed", () => {
        searchBox.setBounds(this.map.getBounds());
      });

      searchBox.addListener("places_changed", () => {
        const places = searchBox.getPlaces();
        console.log(places);

        if (places.length === 0) {
          return;
        }

        const placeLat = places[0].geometry.location.lat();
        const placeLng = places[0].geometry.location.lng();

        const center = new maps.LatLng(placeLat, placeLng);
        const mapConfig = Object.assign({}, { center: center, zoom: zoom });

        this.map = new maps.Map(node, mapConfig);
        this.addMarker(center);
      });

      this.addMarker(center);
    }
  };

  componentDidMount() {
    if (!this.props.authStepSet) {
      this.props.authSteps(9);
    }

    if (this.props.centerAroundCurrentLocation) {
      if (navigator && navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((pos) => {
          const coords = pos.coords;
          this.setState({
            currentLocation: {
              lat: coords.latitude,
              lng: coords.longitude,
            },
          });
        });
      }
    }

    this.loadMap();
  }

  addMarker = (location) => {
    const { google } = this.props;

    const marker = new google.maps.Marker({
      position: location,
      map: this.map,
    });

    this.setState({ ...this.state, markers: marker });
  };

  renderChildren() {
    if (this.map) {
      this.map.addListener("click", (evt) => {
        this.setState({
          ...this.state,
          currentLocation: {
            lat: evt.latLng.lat(),
            lng: evt.latLng.lng(),
          },
        });

        this.state.markers.setPosition(evt.latLng);

        this.props.handleMapValues &&
          this.props.handleMapValues(this.state.currentLocation);
      });
    }
    const { children } = this.props;

    if (!children) return;

    return React.Children.map(children, (c) => {
      if (!c) return;

      return React.cloneElement(c, {
        map: this.map,
        google: this.props.google,
        mapCenter: this.state.currentLocation,
      });
    });
  }

  render() {
    mapStyles.map.height = this.props.height || "50vh";
    const style = Object.assign({}, mapStyles.map);
    return (
      <>
        <div style={{ position: "relative" }}>
          <div style={this.props.inputStyleContainer} className="input-box">
            <form onSubmit={(e) => e.preventDefault()}>
              <input
                style={this.props.inputStyle}
                id="input"
                type="search"
                name="map"
                value={this.state.userData.searchValue.value}
                placeholder="Search your location"
                required={true}
                onChange={(e) => this.inputChangedHandler(e, "searchValue")}
              />
            </form>
          </div>
          <div style={style} ref={this.mapRef}>
            Loading map...
          </div>
          {this.renderChildren()}
          {!this.props.handleMapValues && (
            <UIButton
              className="Map-Button"
              onClick={() =>
                this.props.saveLocation(this.props.match.params.businessId, {
                  lat: this.state.currentLocation.lat,
                  lng: this.state.currentLocation.lng,
                })
              }
            >
              Save Location
            </UIButton>
          )}
        </div>
      </>
    );
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    saveLocation: (businessId, data) =>
      dispatch(actions.saveLocation(businessId, data)),
    authSteps: (currentCount) => dispatch(actions.authSteps(currentCount)),
  };
};

export default connect(null, mapDispatchToProps)(withRouter(CurrentLocation));

CurrentLocation.defaultProps = {
  zoom: 10,
  initialCenter: {
    lat: 22.2884,
    lng: 71.8233,
  },
  centerAroundCurrentLocation: true,
  visible: true,
};
